export default {
	methods:{
		goMap(){
			uni.openLocation({
			  latitude: that.item.latitude,
			  longitude: that.item.longitude,
			  name: "黄龙万科中心",
			  address: "学院路77号",
			  success: function() {
			    console.log("success");
			  },
			  fail(e) {
			    console.log(e);
			  }
			});
		},
		call(){
			uni.makePhoneCall({
			  phoneNumber: "4008123277" //仅为示例
			});
		}
	}
};
